body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
html, body, div#root {
  height: 100%;
  margin: 0;
  padding: 0;
}
body * {
  box-sizing: border-box;
}

:root {
  --size-light: .1rem;
  --size-normal: .25rem;
  --size-hard: .5rem;
  --size-great: 1rem;
  --size-titan: 2rem;
  --size-border: .15rem;

  --transition: .5s;
}

/* **************** */
/* Полоса прокрутки */
/* **************** */
body::-webkit-scrollbar {
  min-width: 5px;
  width: var(--size-hard);
  max-width: 50px;
  min-height: 5px;
  height: .5%;
  max-height: 50px;
}

body::-webkit-scrollbar-button {
  display: none;
  background-color: #666;
}

body::-webkit-scrollbar-track {
  background-color: #999;
}

body::-webkit-scrollbar-track-piece {
  background-color: #ffffff;
}

body::-webkit-scrollbar-thumb {
  height: 3%;
  background-color: #666;
  border-radius: var(--size-hard);
}

body::-webkit-scrollbar-corner {
  background-color: #999;
}

body::-webkit-resizer {
  background-color: #666;
}

body *::-webkit-scrollbar {
  min-width: 5px;
  width: .5%;
  max-width: 50px;
  min-height: 5px;
  height: .5%;
  max-height: 50px;
}

body *::-webkit-scrollbar-track {
  background-color: none;
}

body *::-webkit-scrollbar-track-piece {
  background-color: none;
}

body *::-webkit-scrollbar-thumb {
  height: 3%;
  background-color: #999;
  border-radius: 0.25rem;
}

body *:hover::-webkit-scrollbar-thumb {
  background-color: #666;
}

body *::-webkit-scrollbar-corner {
  background-color: none;
}

body *::-webkit-resizer {
  background-color: #666;
}